let _debounceTimeout = null,
	_throttleRunning = false

/**
 * 
 * @param {Function} 
 * @param {Number} delay ms   
 */
export const debounce = (fn, delay=500) => {
	clearTimeout(_debounceTimeout);
	_debounceTimeout = setTimeout(() => {
		fn();
	}, delay);
}
/**
 * 
 * @param {Function} 
 * @param {Number} delay ms  
 */
export const throttle = (fn, delay=500) => {
	if(_throttleRunning){
		return;
	}
	_throttleRunning = true;
	fn();
	setTimeout(() => {
	    _throttleRunning = false;
	}, delay);
}
/**
 * toast
 */
export const msg = (title = '', param={}) => {
	if(!title) return;
	uni.showToast({
		title,
		duration: param.duration || 1500,
		mask: param.mask || false,
		icon: param.icon || 'none'
	});
}
/**
 * 
 * @return {Boolean}
 */
export const isLogin = (options={}) => {
	const token = uni.getStorageSync('token');
	if(token){
		return true;
	}else{
        uni.showToast({
            title:'',
            icon:'none'
        })
        setTimeout(()=>{
            uni.navigateTo({
            	url: '/pages/Login/Login'
            })
        },1500)
        return false
    }
}
/**
 * 
 * @param {Number} preIndex1
 * @return {Object} 
 */
export const prePage = (preIndex = 1) => {
	const pages = getCurrentPages();
	const prePage = pages[pages.length - (preIndex + 1)];
	return prePage.$vm;
}
/**
 *  Y-m-d H:i:s
 * @param {String} format Y-m-d H:i:s
 * @param {Number} timestamp    
 * @return {String}
 */
export const date = (format, timeStamp) => {
	if('' + timeStamp.length <= 10){
		timeStamp = + timeStamp * 1000;
	}else{
		timeStamp = + timeStamp;
	}
	let _date = new Date(timeStamp),
		Y = _date.getFullYear(),
		m = _date.getMonth() + 1,
		d = _date.getDate(),
		H = _date.getHours(),
		i = _date.getMinutes(),
		s = _date.getSeconds();
	
	m = m < 10 ? '0' + m : m;
	d = d < 10 ? '0' + d : d;
	H = H < 10 ? '0' + H : H;
	i = i < 10 ? '0' + i : i;
	s = s < 10 ? '0' + s : s;

	return format.replace(/[YmdHis]/g, key=>{
		return {Y,m,d,H,i,s}[key];
	});
}
//
export const getUnique = array => {
	let obj = {}
    return array.filter((item, index) => {
		let newItem = item + JSON.stringify(item)
		return obj.hasOwnProperty(newItem) ? false : obj[newItem] = true
	})
}
// 
export const checkStr = (str, type) => {
	switch (type) {
		case 'mobile': //
			return /^1[3|4|5|6|7|8|9][0-9]{9}$/.test(str);
		case 'tel': //
			return /^(0\d{2,3}-\d{7,8})(-\d{1,4})?$/.test(str);
		case 'card': //
			return /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(str);
		case 'mobileCode': //6
			return /^[0-9]{6}$/.test(str)
		case 'pwd': //8-16
			return /^(?![0-9]+$)(?![a-z]+$)(?![A-Z]+$)(?!([^(0-9a-zA-Z)])+$)^.{8,16}$/.test(str)
		case 'payPwd': // 6
			return /^[0-9]{6}$/.test(str)
		case 'postal': //
			return /[1-9]\d{5}(?!\d)/.test(str);
		case 'QQ': //QQ
			return /^[1-9][0-9]{4,9}$/.test(str);
		case 'email': //
			return /^[\w-]+(\.[\w-]+)*@[\w-]+(\.[\w-]+)+$/.test(str);
		case 'money': //(2)
			return /^\d*(?:\.\d{0,2})?$/.test(str);
		case 'URL': //
			return /(http|ftp|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&:/~\+#]*[\w\-\@?^=%&/~\+#])?/.test(str)
		case 'IP': //IP
			return /((?:(?:25[0-5]|2[0-4]\\d|[01]?\\d?\\d)\\.){3}(?:25[0-5]|2[0-4]\\d|[01]?\\d?\\d))/.test(str);
		case 'date': //
			return /^(\d{4})\-(\d{2})\-(\d{2}) (\d{2})(?:\:\d{2}|:(\d{2}):(\d{2}))$/.test(str) || /^(\d{4})\-(\d{2})\-(\d{2})$/
				.test(str)
		case 'number': //
			return /^[0-9]$/.test(str);
		case 'english': //
			return /^[a-zA-Z]+$/.test(str);
		case 'chinese': //
			return /^[\\u4E00-\\u9FA5]+$/.test(str);
		case 'lower': //
			return /^[a-z]+$/.test(str);
		case 'upper': //
			return /^[A-Z]+$/.test(str);
		case 'HTML': //HTML
			return /<("[^"]*"|'[^']*'|[^'">])*>/.test(str);
		default:
			return true;
	}
}

// 
export const dateFormat = function(fmt = 'YYYY-MM-DD', date = new Date()) {
  const time = new Date(date)
  const weekArr = ['', '', '', '', '', '', '']
  var o = {
    'M+': time.getMonth() + 1, //
    'D+': time.getDate(), //
    'h+': time.getHours(), //
    'm+': time.getMinutes(), //
    's+': time.getSeconds(), //
    'q+': Math.floor((time.getMonth() + 3) / 3), //
    S: time.getMilliseconds(), //
    W: time.getDay(), //
  }
  if (/(Y+)/.test(fmt))
    fmt = fmt.replace(RegExp.$1, (time.getFullYear() + '').substr(4 - RegExp.$1.length))
  for (var k in o)
    if (new RegExp('(' + k + ')').test(fmt))
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1 == 'W'
          ? weekArr[o[k]]
          : RegExp.$1.length == 1
          ? o[k]
          : ('00' + o[k]).substr(('' + o[k]).length)
      )
  return fmt
}
